/*
 * 多图片展示
 * int: 更改值 重新初始化
 * d: 数据
 * type: 图片显示类型 real | full
 */

<template>
  <div :class="cName" @mouseover="footer = true" @mouseout="footer = false">
    <div class="cB preCB" @click="setImg('pre')"></div>
    <div class="cB nextCB" @click="setImg('next')"></div>
    <div class="hoverLine imgTitle" v-if="title">{{title}}</div>
    <div class="imgBox" ref="box"></div>
    <transition name="bottomFade">
      <div v-show="footer" class="hoverLine footer" @click="play">{{footerText}}</div>
    </transition>
    <div class="radioIcon" v-if='radio'>
      <i @click="setImg(c)" :class="c == i ? 'active' : ''" v-for="(v, c) in data" :key="'ico' + c"></i>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    theme: {
      type: String,
      default: ''
    },
    int: {
      type: Number,
      default: -1
    },
    d: {
      type: Array,
      default: function () {
        return []
      }
    },
    type: {
      type: String,
      default: 'full'
    },
    speed: {
      type: Number,
      default: 1500
    },
    radio: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      data: [],
      title: false,
      img: false,
      i: 0,
      state: false,
      footer: false,
      timer: null
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      window.addEventListener('resize', this.initEvent)
    })
  },
  beforeDestroy: function () {
    window.removeEventListener('resize', this.initEvent)
    this.stop()
  },
  methods: {
    initEvent: function () {
      this.initImg(this.img)
    },
    clear: function () {
      let box = this.$refs.box
      if (!box) return
      box.innerHTML = ''
    },
    initData: function (v) {
      let { d } = this.$props
      this.data = d
      this.setImg(0)
    },
    setImg: function (key) {
      let { i, data } = this
      switch (key) {
        case 'next':
          i = i + 1 < data.length ? i + 1 : 0
          break
        case 'pre':
          i = i - 1 < 0 ? data.length - 1 : i - 1
          break
        default:
          i = key
          break
      }
      this.i = i
      let v = data[i] || {}
      this.img = v['url']
      this.title = v['name']
    },
    initImg: function (v) {
      if (!v) return
      this.clear()
      let { type } = this.$props
      let img = document.createElement('img')
      img.src = v
      img.onload = () => {
        if (img.src !== this.img) return
        let box = this.$refs.box
        if (!box) return
        let bw = box.offsetWidth, bh = box.offsetHeight
        let iw = img.width, ih = img.height
        let w = iw, h = ih
        if (bw / bh >= iw / ih) {
          h = Math.min(bh, ih)
          w = h * iw / ih
        } else {
          w = Math.min(bw, iw)
          h = w * ih / iw
        }
        if (type === 'full') {
          img.width = bw
          img.height = bh
        } else {
          img.style.marginTop = (bh - h) / 2 + 'px'
          img.width = w
          img.height = h
        }
        img.draggable = false
        if (this.img !== img.src) return
        box.appendChild(img)
      }
      img.onerror = () => {
        this.clear()
      }
    },
    play: function () {
      let { state } = this
      let { speed } = this.$props
      let s = !state
      this.state = s
      this.stop()
      if (!s) return
      this.timer = setInterval(() => {
        this.setImg('next')
      }, speed)
    },
    stop: function () {
      clearInterval(this.timer)
      this.timer = null
    }
  },
  computed: {
    cName: function () {
      return 'easyImg ' + this.theme
    },
    footerText: function () {
      let { state } = this
      return state ? '暂 停' : '播 放'
    }
  },
  watch: {
    int: function () {
      this.initData()
    },
    img: function (v) {
      this.initImg(v)
    }
  }
}
</script>

<style type="text/less" lang="less" scoped>
@import "./index.less";
</style>
